.section-header{
    font-family: 'Titan One';
    font-weight: 400;
    font-size: 18px;
    line-height: 1.16;
    text-transform: uppercase;

    color: var(--hero-bg);

    @media screen and (min-width: 768px){
        
        font-size: 26px;
        line-height: 1.15;
    }
    @media screen and (min-width: 1280px){
        width: auto;
        font-size: 30px;
        line-height: 1.13;
        letter-spacing: 0.04em;
    }
}
.section-header__bold{
    font-family: 'Titan One';
    font-weight: 400;
    font-size: 34px;
    line-height: 1.08;
    text-transform: uppercase;

    color: var(--text-accent);
    @media screen and (min-width: 768px){
        
        font-size: 48px;
        line-height: 1.14;
    }
    @media screen and (min-width: 1280px){
        
        font-size: 58px;
        line-height: 1.13;
        letter-spacing: 0.06;
    }
}